
import { defineComponent, PropType } from "vue";

// TODO: fix v-tooltip empty divs

export default defineComponent({
  name: "NavbarList",
  props: {
    list: {
      type: Array as PropType<Array<{ name: string; icon: string }>>,
      required: true,
    },
    rootLink: {
      type: String,
      required: true,
    },
    order: {
      type: Number,
      required: true,
    },
    navCollapsed: {
      type: Boolean,
      required: true,
    },
  },
});
